@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

#root .psm-swap-container {
  display: flex;
  justify-content: space-between;
  margin: 90px auto 65px;
  max-width: calc(50px + 1200px);
  padding: 15px 25px;
  align-items: center;
  overflow: hidden;

  .psm-description {
    // max-width: 650px;
    margin-right: -80px;
    position: relative;
    margin-bottom: 15px;
    background: #31313e;
    padding: 50px 30px;
    text-align: start;
    white-space: nowrap;
    border-radius: 30px;
    color: #fff;
    width: 650px;

    .bg-card {
      white-space: nowrap;
      text-align: center;
      border-radius: 30px;
      width: 650px;

      &.card1 {
        transform: rotate(-5deg);
        background: rgba(49, 49, 62, 0.4);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }

      &.card2 {
        transform: rotate(-2.5deg);
        background: rgba(49, 49, 62, 0.7);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }

    .des-content {
      position: relative;
      width: 500px;

      .psm-book {
        position: absolute;
        left: 340px;
        top: -60px;
      }

      .des-content-title {
        font-weight: 600;
        font-size: 30px;
        line-height: 42px;
      }

      .des-item-title {
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        margin-top: 30px;
        white-space: pre-wrap;

        &:nth-child(3) {
          margin-top: 40px;
        }
      }

      .des-item-content {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        margin-top: 10px;
        opacity: 0.6;
        white-space: pre-wrap;
      }

      .psm-des-footer {
        margin-top: 30px;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .des-footer-btn {
          padding: 6px 15px;
          border-radius: 6px;
          color: #9A8AFF;
          font-weight: 600;
          font-size: 16px;
          line-height: 22px;
          cursor: pointer;
          margin-left: 10px;
          flex-wrap: wrap;
          background: #3B3D4B;

          a {
            color: #9A8AFF;
          }

          &:first-child {
            margin-left: 0;
            padding: 6px 33px 6px 15px;
            background: #3B3D4B url(../images/psm/hover-link.svg) calc(100% - 15px) no-repeat;
          }
        }
      }
    }
  }

  .psm-swap {
    width: min(630px, 90vw);
    background: #21222f;
    padding: 34px;
    margin-bottom: 15px;
    text-align: center;
    overflow-wrap: break-word;
    display: inline-block;
    margin: 0px;
    position: relative;
    z-index: 0;
    color: #fff;
    border-radius: 20px;

    @keyframes rotate {
      from {
        --angle: 0deg;
      }
      to {
        --angle: 360deg;
      }
    }

    &:before {
      border-radius: 30px;
      padding: 4px;

      background: linear-gradient(135deg, #66ffed, #bf5aee, #67a4ff, #ff9141);
      -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor;
      mask-composite: exclude;

      content: "";
      position: absolute;
      z-index: 2;
      inset: 0;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;

      @supports (background: paint(something)) {
        background: linear-gradient(var(--angle), #66ffed, #bf5aee, #67a4ff, #ff9141);
        animation: 5s rotate linear infinite;
      }
    }

    .psm-swap-title {
      font-weight: 700;
      font-size: 40px;
      line-height: 48px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .psm-swap-title-text {
        font-weight: 600;
        font-size: 40px;
        line-height: 56px;
        margin-left: 10px;
      }
    }

    .psm-swap-amount-detail {
      display: none;
      background: #3B3D4B;
      border-radius: 6px;
      padding: 6px 17.5px;
      font-size: 16px;
      line-height: 22px;
      color: #9A8AFF;
      cursor: pointer;
    }

    .psm-swap-des {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      opacity: 0.6;
      margin-top: 5px;
      text-align: left;
      margin-left: 10px;
    }

    .psm-swap-box {
      display: flex;
      flex-direction: column;
      position: relative;
      margin-top: 30px;

      .swap-arrow {
        position: absolute;
        height: 40px;
        width: 40px;
        background-size: cover;
        background-repeat: no-repeat;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
        -webkit-transform: translateX(-50%) translateY(-50%);
        cursor: pointer;
        background-image: url(../images/psm/swap-box-arrow.svg);

        &:hover {
          background-image: url(../images/psm/swap-box-arrow-purple.svg);
        }
      }

      .swap-input-box {
        background: #3b3d4b;
        border-radius: 15px;
        padding: 20px;
        border: 1px solid #3b3d4b;

        &.swap-from {
          margin-bottom: 10px;
        }

        .swap-input-title {
          justify-content: space-between;
          align-items: center;
          display: flex;
          flex-direction: row;

          .swap-input-title-text {
            font-weight: 700;
            font-size: 28px;
            line-height: 34px;
            color: #ffffff;
          }

          .title-info {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .swap-balance-text {
              font-size: 14px;
              font-weight: 500;
              color: rgba(255, 255, 255, 0.6);
            }

            .swap-balance-value {
              font-size: 14px;
              font-weight: 600;
              color: #ffffff;
              margin-left: 6px;
              display: inline-flex;
              align-items: center;

              span {
                margin-right: 5px;
              }

              img {
                margin-left: 5px;
              }
            }

            .swap-input-title-max {
              font-weight: 500;
              font-size: 14px;
              color: #9A8AFF;
              margin-left: 10px;
              cursor: pointer;
            }
          }
        }

        .swap-input {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 24px;

          .ant-input {
            height: 53px;
            white-space: nowrap;
            // overflow: hidden;
            // text-overflow: ellipsis;
            padding-right: 16px;
            background: transparent;
            border: none;
            padding-left: 0;
            font-weight: 700;
            font-size: 44px;
            line-height: 53px;
            color: #ffffff;
            &::placeholder {
              font-size: 28px;
              transform: translateY(-7px);
              -ms-transform: translateY(-7px);
              -webkit-transform: translateY(-7px);
              color: rgba(255, 255, 255, 0.1);
            }
          }

          .ant-select {
            color: #333;
            font-weight: bold;
            font-size: 14px;
            line-height: 16px;
            display: inline-flex;
            align-items: center;
            width: 131px;

            .ant-select-selector {
              background: #343642;
              border-radius: 10px;
              font-size: 20px;
              line-height: 24px;
              color: #ffffff;
              width: 131px;
              height: 48px;
              border: none;
              padding: 0 10px;

              .ant-select-selection-search-input {
                font-size: 16px;
              }

              .ant-select-selection-item {
                display: flex;
                align-items: center;
                padding-right: 0;
                line-height: 24px;
                justify-content: flex-start;
                margin-right: 20px;
                color: #fff;
                > img {
                  width: 28px;
                  height: 28px;
                  margin-right: 8px;
                }
              }
            }
            .ant-select-arrow {
              right: 10px;
              top: 50%;
            }
          }
          .ant-select-open .ant-select-arrow {
            transform: rotate(180deg);
          }
        }
      }

      .swap-error-tip {
        margin-top: 13px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        min-height: 20px;
        color: #E06B63;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &.vhide {
          visibility: hidden;
        }
      }
    }

    .swap-fee {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 5px;
      font-size: 18px;
      min-height: 22px;
      img {
        margin-left: 5px;
        cursor: pointer;
      }
    }

    .submit-btn {
      background: linear-gradient(79.72deg, #4504fe 4.86%, #e64dff 103.07%);
      font-style: normal;
      display: flex;
      align-items: center;
      border-radius: 15px;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      width: 100%;
      height: 70px;
      justify-content: center;
      margin-top: 30px;
      cursor: pointer;

      &.disabled {
        color: rgba(255, 255, 255, 0.4);
        cursor: not-allowed;
      }
    }
  }

  .ant-modal-root {
    .psm-detail-modal.ant-modal {
      width: 900px !important;
      .ant-modal-content {
        // background: linear-gradient(180deg, #222229 0%, #18181F 100%);
        background: #222229;
        border-radius: 20px;
        color: #fff;
        .ant-modal-body {
          padding: 40px !important;
          min-height: 420px;
          .psm-amount-modal-content {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            .top {
              padding: 60px 0 36px 50px;
              text-align: left;
              width: 100%;
              background: url(../images/psm/bg-j.jpg) no-repeat;
              background-size: auto 100%;
              .tip {
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: rgba(255, 255, 255, 0.6);
                display: inline-flex;
                align-items: center;
                img {
                  margin-left: 5px;
                }
              }
              .balance {
                font-weight: 700;
                font-size: 44px;
                line-height: 53px;
                padding-left: 50px;
                margin-top: 13px;
                background: url(../images/psm/psm-usdd.svg) left no-repeat;
              }
            }
            .table {
              display: flex;
              align-items: center;
              flex-direction: column;
              background: #191A1A;
              border-radius: 16px;
              width: 100%;
              // padding: 0 30px;
              .tr {
                display: inline-flex;
                align-items: center;
                padding: 30px 60px;
                justify-content: space-between;
                width: 100%;
                border-bottom: 1px dashed rgba(255, 255, 255, 0.2);
                border-radius: 16px 16px 0 0;

                &.th {
                  height: 40px;
                  line-height: 40px;
                  padding: 9px 60px;
                  border-bottom: none;

                  font-weight: 500;
                  font-size: 16px;
                  line-height: 22px;
                  color: rgba(255, 255, 255, 0.7);
                  background: rgba(0, 0, 0, 0.2);

                  .td:nth-child(2) {
                    transform: translateX(23px);
                    -ms-transform: translateX(23px);
                    -webkit-transform: translateX(23px);
                  }
                }

                &:last-child {
                  border-bottom: none;
                }

                .td {
                  text-align: right;
                  width: calc(33% - 33px);
                  font-weight: 600;
                  font-size: 16px;
                  line-height: 19px;
                  img {
                    margin-top: -3px;
                    margin-left: 7px;
                  }

                  &:first-child {
                    width: 71px;
                    text-align: left;
                  }

                  &.link {
                    color: #746BB7;
                    cursor: pointer;
                    background: url(../images/psm/regular-link.svg) 44px 2px no-repeat;
                    padding-right: 16px;
                    a {
                      color: #746BB7;
                    }

                    &:hover {
                      background: url(../images/psm/hover-link.svg) 44px 2px no-repeat;
                      a {
                        color: #998FDF;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      .ant-modal-close {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 10;
        padding: 0;
        color: rgba(0, 0, 0, 0.45);
        font-weight: 700;
        line-height: 1;
        text-decoration: none;
        background: transparent;
        border: 0;
        outline: 0;
        cursor: pointer;
        transition: color 0.3s;
        min-width: unset;
      }
    }
    .psm-contracts-modal.ant-modal {
      width: 430px !important;
      .ant-modal-content {
        background: #fff;
        border-radius: 15px;
        color: #333;
        .ant-modal-body {
          padding: 24px 0px 36px !important;
          .psm-contracts-modal-content {
            .title {
              font-weight: 500;
              font-size: 18px;
              line-height: 20px;
              margin-bottom: 20px;
              padding: 0 30px;
            }
            .content {
              padding: 0 30px;
              max-height: 420px;
              overflow: auto;
              &::-webkit-scrollbar {
                width: 6px;
                height: 6px;
                background: transparent;
              }

              &::-webkit-scrollbar-thumb {
                background: transparent;
                border-radius: 10px;
              }

              &:hover::-webkit-scrollbar-thumb {
                background: #DEDEDE;
              }

              &:hover::-webkit-scrollbar-track {
                background: transparent;
              }
              .item {
                text-align: left;
                .item-title {
                  font-weight: 600;
                  font-size: 14px;
                  line-height: 20px;
                }
                .ul {
                  background: #F5F6FF;
                  border-radius: 5px;
                  margin-top: 10px;
                  padding: 17px 10px 17px 16px;
                  .li {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 14px;
                    margin-top: 20px;

                    &:first-child {
                      margin-top: 0;
                    }
                    .name {
                      color: #666;
                    }
                    .address {
                      color: #5915E1;
                      margin-top: 5px;
                      display: flex;
                      align-items: center;
                      position: relative;
                      justify-content: space-between;

                      .aaa {
                        top: 0;
                        margin-top: 20px !important;
                        right: -35px;
                      }

                      a {
                        display: inline-block;
                        // width: 318px;
                        width: calc(100% - 26px);
                        background: url(../images/psm/contract-link.svg) right no-repeat;
                      }

                      img {
                        cursor: pointer;
                      }
                    }
                  }
                }
              }
              .tip {
                margin: 10px 17px 40px;
                font-weight: 500;
                font-size: 12px;
                line-height: 17px;
                text-align: left;
              }
            }
          }
        }
      }
      .ant-modal-close {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 10;
        padding: 0;
        color: rgba(0, 0, 0, 0.45);
        font-weight: 700;
        line-height: 1;
        text-decoration: none;
        background: transparent;
        border: 0;
        outline: 0;
        cursor: pointer;
        transition: color 0.3s;
        min-width: unset;
        height: unset;
      }
    }
  }

  .ant-modal-mask {
    background: rgba(0, 0, 0, 0.7);
  }
}

html body .psm-select.ant-select-dropdown {
  background: #2c2e38;
  padding: 0;
  border-radius: 10px;
  .ant-select-item {
    border-radius: 8px;
    margin: 0px;
    padding: 7px 10px;
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    background: #2c2e38!important;

    &:hover {
      background: #5915e1 !important;
    }

    .ant-select-item-option-content {
      font-size: 20px;
      line-height: 24px;
      display: flex;
      align-items: center;
      width: 100%;
      padding: 4px 0;
      img {
        width: 28px;
        height: 28px;
        margin-right: 8px;
      }
    }
  }
}

