@media screen and (max-width: 1079px) {
  #root .psm-swap-container {
    margin-top: 30px;
    font-size: .83333vw;
    flex-direction: column-reverse;

    .psm-swap {
      padding: 15px;
      width: 100%;
      border-radius: 15px;
      &::before {
        border-radius: 15px;
        padding: 2px;
      }
      .psm-swap-title {
        flex-direction: column;
        .psm-swap-title-text {
          font-size: 24px;
          margin-left: 0;
        }
      }
      .psm-swap-amount-detail {
        // display: inline-block;
        display: none;
        margin-top: 12px;

        font-weight: 600;
        padding: 6px 15px;
        font-size: 14px;
        line-height: 18px;
      }
      .psm-swap-des {
        font-size: 12px;
        margin-top: 3px;
      }
      .psm-swap-box {
        margin-top: 15px;
        .swap-input-box {
          padding: 10px;
          &.swap-from {
            margin-bottom: 5px;
            height: 115px;
            display: flex;
            flex-direction: column;
          }
          .swap-input-title {
            .swap-input-title-text {
              font-size: 14px;
              line-height: 17px;
            }
            .title-info {
              margin-left: 5px;
              .swap-balance-text, .swap-balance-value, .swap-input-title-max {
                font-size: 12px;
                line-height: 17px;
                flex-wrap: wrap;
                justify-content: center;
              }
            }
          }
          .swap-input {
            margin-top: 19px;
            .ant-input {
              height: 29px;
              line-height: 29px;
              font-size: 24px;
              padding: 1px 5px 1px 0;
              &::placeholder {
                font-size: 18px;
                line-height: 22px;
                transform: translateY(-2px);
                -ms-transform: translateY(-2px);
                -webkit-transform: translateY(-2px);
              }
            }
            .ant-select {
              .ant-select-selector {
                height: 32px;
                font-size: 14px;
                line-height: 17px;
                width: 95px;
                padding: 0 8px;
                .ant-select-selection-item {
                  margin-right: 15px;
                  > img {
                    width: 20px;
                    height: 20px;
                    margin-right: 6px;
                  }
                }
              }
              .ant-select-arrow {
                width: 8px;
                right: 8px;
                img {
                  width: 8px;
                }
              }
            }
          }
        }
        .swap-error-tip {
          font-size: min(0.873rem, 12px);
          margin-top: 5px;
        }
        .swap-arrow {
          width: 20px;
          height: 20px;
        }
      }
      .swap-fee {
        font-weight: 700;
        font-size: 14px;
      }
      .submit-btn {
        height: 40px;
        font-size: 14px;
        line-height: 17px;
        margin-top: 33px;
        border-radius: 10px;
      }
    }
    .psm-description {
      width: 100%;
      margin-right: 0;
      margin-top: 20px;
      padding: 10px 10px 30px 10px;
      border-radius: 15px;
      .bg-card {
        display: none;
      }
      .des-content {
        width: 100%;
        .psm-book {
          left: unset;
          right: 0px;
          top: -10px;
          width: 90px;
        }
        .des-content-title {
          font-size: 24px;
          line-height: 34px;
          width: calc(100% - 85px);
        }
        .des-item-title {
          margin-top: 30px;
          font-size: 16px;
          &:nth-child(3) {
            width: calc(100% - 85px);
          }
        }
        .des-item-content {
          font-size: 12px;
          line-height: 17px;
          margin-top: 10px;
        }

        // todo, 移动端待定
        .psm-des-footer {
          margin-top: 30px;

          .des-footer-btn {
            padding: 6px 15px;
            font-size: 14px;
            line-height: 18px;
          }
        }
      }
    }
    .ant-modal-root {
      .psm-detail-modal.ant-modal {
        max-width: calc(100% - 30px);
        .ant-modal-content {
          .ant-modal-body {
            padding: 10px !important;
            .psm-amount-modal-content {
              .top {
                margin-top: 12px;
                padding: 18px 0 20px 10px;
                background: no-repeat;
                background-image: url(../images/psm/bg-j.jpg);
                background-size: auto 100%;
                background-position: 50%;
                .tip {
                  font-size: 13px;
                  line-height: 18px;
                }
                .balance {
                  font-size: 20px;
                  line-height: 24px;
                  padding-left: 34px;
                  margin-top: 5px;
                  background: url(../images/psm/psm-usdd.svg) left no-repeat;
                  background-size: 24px;
                }
              }
              .table {
                // padding: 10px;
                .tr {
                  padding: 20px 5px;

                  &.th {
                    height: unset;
                    padding: 7px 5px;

                    // font-size: min(0.623rem, 12px);
                    font-size: 12px;
                    line-height: 12px;

                    .td {
                      line-height: 15px;
                    }

                    .td:nth-child(1) {
                      width: 30px;
                    }

                    .td:nth-child(2) {
                      transform: translateX(-0px);
                      -ms-transform: translateX(-0px);
                      -webkit-transform: translateX(-0px);
                      width: 33%;
                    }
                  }

                  .td {
                    width: calc(33% - 20px);
                    // font-size: min(0.561rem, 12px);
                    font-size: 12px;
                    line-height: 12px;
                    img {
                      margin-top: -1.5px;
                      margin-left: 3px;
                      width: 12px;
                      height: 12px;
                    }

                    &:first-child {
                      width: 51px;
                    }

                    &.link {
                      color: #746BB7;
                      cursor: pointer;
                      background: url(../images/psm/regular-link.svg) 32px -1px no-repeat;
                      background-size: 14px;
                      padding-right: 16px;
                      a {
                        color: #746BB7;
                      }

                      &:hover {
                        background: url(../images/psm/hover-link.svg) 32px -1px no-repeat;
                        background-size: 14px;
                        a {
                          color: #998FDF;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .ant-modal-close .ant-modal-close-x {
          width: 42px;
          height: 42px;
          line-height: 42px;
        }
      }
    }
  }
  #root .psm-swap-container .ant-modal-root .psm-contracts-modal.ant-modal .ant-modal-content .ant-modal-body .psm-contracts-modal-content .content {
    padding: 0 15px;
    .item .ul .li .address a {
      display: inline-block;
      width: calc(100% - 20px);
    }
  }
  html body .psm-select.ant-select-dropdown {
    .ant-select-item {
      padding: 5px 8px;
      .ant-select-item-option-content {
        font-size: 14px;
        line-height: 24px;
        img {
          width: 20px;
          height: 20px;
          margin-right: 8px;
        }
      }
    }
  }
}